.modal-overlay {
  display: none;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 999;
}
.modal-overlay.active {
  display: flex;
  .modal-body {
    filter: drop-shadow(-15px 18px 40px rgba(0, 0, 0, 0.5));
  }
}

.modal-overlay {
  .modal-body {
    background-color: white;
    position: relative;
    min-width: 300px;
    .modal-contents {
      display: flex;
    }
    .close {
      cursor: pointer;
      position: absolute;
      right: 15px;
      top: 15px;
      height: 20px;
      width: 20px;
      fill: #577590;
    }
  }
}
